@import url(https://fonts.googleapis.com/css?family=Karla|Roboto:300,500&display=swap);
body {
  font-family: 'Karla', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: #2962FF;
}

p {
  font-size: 24px;
  line-height: 1.6;
}
.container {
  max-width: 1720px;
  padding: 0 64px;
  margin: auto;
  padding-left: calc(100vw - 100%); }

header {
  display: flex;
  padding-top: 24px; }
  header .site-logo {
    flex: none;
    height: 112px; }
  header nav {
    flex: 1 1 auto;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 4px;
    text-align: right; }
    header nav ul {
      list-style: none; }
      header nav ul li {
        display: inline; }
        header nav ul li a {
          display: inline-block;
          color: #333;
          text-transform: uppercase;
          padding: 8px 16px;
          position: relative;
          outline: 0; }
          header nav ul li a::after {
            content: '';
            width: 70%;
            height: 3px;
            display: inline-block;
            opacity: 0;
            border-radius: 2px;
            position: absolute;
            left: calc(50% - 70%/2);
            bottom: 0px;
            -webkit-transition: opacity 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
            transition: opacity 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
            transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;
            transition: opacity 150ms ease-in-out, transform 150ms ease-in-out, -webkit-transform 150ms ease-in-out;
            -webkit-transform: translateY(-4px);
                    transform: translateY(-4px); }
          header nav ul li a.active::after, header nav ul li a:hover::after, header nav ul li a:focus::after {
            opacity: 1;
            -webkit-transform: translateY(0);
                    transform: translateY(0); }
          header nav ul li a.home-link.active::after, header nav ul li a.home-link:hover::after, header nav ul li a.home-link:focus::after {
            background: #f9d29f; }
          header nav ul li a.work-link.active::after, header nav ul li a.work-link:hover::after, header nav ul li a.work-link:focus::after {
            background: #c6e4b8; }
          header nav ul li a.resume-link.active::after, header nav ul li a.resume-link:focus::after, header nav ul li a.resume-link:hover::after {
            background: #f7e594; }
          header nav ul li a.contact-link.active::after, header nav ul li a.contact-link:focus::after, header nav ul li a.contact-link:hover::after {
            background: #ecbdb4; }
        header nav ul li:not(:first-child) {
          margin-left: 16px; }

.hero {
  margin: auto;
  display: block; }

section.row {
  display: flex;
  max-width: 90%;
  margin: auto; }
  section.row img {
    max-width: 100%; }
  section.row .flex-none {
    flex: none; }
  section.row .flex-grow {
    flex: 1 1; }
  section.row.hello-section {
    margin-top: 300px; }
    section.row.hello-section .flex-grow {
      margin-left: 80px; }

footer {
  font-family: 'Roboto', sans-serif;
  background: #f9d29f6b;
  margin-top: 120px;
  padding: 64px 32px; }
  footer .container {
    display: flex;
    min-height: 320px;
    align-items: center; }
    footer .container nav {
      display: block;
      font-family: 'Roboto', sans-serif;
      font-weight: 500;
      font-size: 18px;
      letter-spacing: 4px;
      text-transform: uppercase; }
      footer .container nav ul {
        list-style: none;
        margin: 0;
        padding: 0; }
        footer .container nav ul li a {
          display: inline-block;
          color: #333;
          padding: 8px 16px;
          outline: 0; }
    footer .container nav,
    footer .container .right-col {
      flex: 1 1; }
    footer .container img {
      flex: 2 1; }
    footer .container nav,
    footer .container .right-col {
      padding: 0 64px; }
    footer .container .page-break {
      width: 540px;
      display: block;
      margin: auto;
      padding: 32px 64px; }

